import { createPinia, defineStore } from 'pinia'
import { ref } from 'vue'

const rsaDefineStore = defineStore('rsa', {
  state: () => ({
    publicKey: ref<string>('MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCD5XD8TEL7d6EwN6WohUhLMPbx7LumqTY4hD0wHQDVB8QcOtyKiHTJEL+KnmY662gkDJnxiaeMUqX5c2AneXf3wLCYi6I8JmFqSNhOdxNdo/YvklPcmBAmxpW2grZdO4J2MWVykHrAMD07YJOKXDZcwe4HQgpoIH7hKvalcc4QnQIDAQAB'), // RSA 
    privateKey: ref<string>('') // RSA 
  }),
  getters: {
    /**
     * RSA 
     * @param state
     */
    getPublicKey (state) {
      return state.publicKey
    },
    /**
     * RSA 
     * @param state
     */
    getPrivateKey (state) {
      return state.privateKey
    }
  },
  actions: {
    /**
     * RSA 
     * @param publicKey
     */
    setPublicKey (publicKey: string) {
      this.publicKey = publicKey
    },
    /**
     * RSA 
     * @param privateKey
     */
    setPrivateKey (privateKey: string) {
      this.privateKey = privateKey
    }
  }
})

const rsaStore = rsaDefineStore(createPinia())

// 
rsaStore.$subscribe((mutation, state) => {
  // 
  localStorage.setItem(rsaStore.$id, JSON.stringify({ ...state }))
})

// 
const aesStoreOld = localStorage.getItem(rsaStore.$id)
if (aesStoreOld) {
  // 
  rsaStore.$state = JSON.parse(aesStoreOld)
}

// 
export default rsaStore
