<template>
  <el-row justify="center">
    <el-col :span="22" class="text-center">
      <h1>RSA </h1>
    </el-col>
  </el-row>

  <br>

  <el-row justify="center" :gutter="20">
    <el-col :span="22">
      <el-button @click="generate" class="generate-button"></el-button>
      <el-select v-model="bits" placeholder="RSA" class="generate-button-select w-150px">
        <el-option v-for="item in bitsOptions" :key="item.value" :label="item.label" :value="item.value"/>
      </el-select>
    </el-col>
  </el-row>

  <br>

  <el-row justify="center" :gutter="20">
    <el-col :span="11">
      <el-input v-model="publicKey" class="originalText-input" placeholder="RSA" type="textarea" @dblclick="dblclick"
                rows="10" data-dblclick="RSA"/>
    </el-col>
    <el-col :span="11">
      <el-input v-model="privateKey" class="ciphertext-input" placeholder="RSA" type="textarea" @dblclick="dblclick"
                rows="10" data-dblclick="RSA"/>
    </el-col>
  </el-row>

  <br>

  <el-row justify="center" :gutter="20">
    <el-col :span="11">
      <el-button @click="publicKeyEncrypt" class="encrypt-button w-100%"></el-button>
    </el-col>
    <el-col :span="11">
      <el-row :gutter="20">
        <el-col :span="12">
          <el-button @click="privateKeyEncrypt" class="encrypt-button w-100%"></el-button>
        </el-col>
        <el-col :span="12">
          <el-button @click="privateKeyDecrypt" class="decrypt-button w-100%"></el-button>
        </el-col>
      </el-row>
    </el-col>
  </el-row>

  <br>

  <el-row justify="center" :gutter="20">
    <el-col :span="11">
      <el-input v-model="originalText" class="originalText-input" placeholder="" type="textarea" @dblclick="dblclick"
                rows="15" data-dblclick=""/>
    </el-col>
    <el-col :span="11">
      <el-input v-model="ciphertext" class="ciphertext-input" placeholder="" type="textarea" @dblclick="dblclick"
                rows="15" data-dblclick=""/>
    </el-col>
  </el-row>

</template>

<script setup lang="ts">
import { ref, watch } from 'vue'
// @ts-ignore
import jsrsasign from 'jsrsasign'
import JsEncrypt from 'jsencrypt'
import { ElMessage, ElMessageBox } from 'element-plus/es'

import dblclick from '../../utils/clipboard'
import rsaStore from '../../store/rsa'

// 
const warning = ref<boolean>(false)

// 
const originalText = ref<string|null>('my message')
// 
const ciphertext = ref('')

// 
const publicKeyEncrypt = () => {
  if (publicKey.value === null || publicKey.value === '') {
    ElMessage.error('')
    return
  }

  if (originalText.value === null || originalText.value === '') {
    ElMessage.error('')
    return
  }

  JsEncrypt.prototype.setPrivateKey('') // 
  JsEncrypt.prototype.setPublicKey(publicKey.value)
  const encrypt = JsEncrypt.prototype.encrypt(originalText.value)

  if (encrypt === false) {
    ElMessage.error('')
    return
  }

  ciphertext.value = encrypt
  ElMessage({ message: '', type: 'success' })
}

// 
const privateKeyEncrypt = () => {
  const privateKeyValue = privateKey.value
  if (privateKeyValue === null || privateKeyValue === '') {
    ElMessage.error('')
    return
  }

  if (originalText.value === null || originalText.value === '') {
    ElMessage.error('')
    return
  }

  JsEncrypt.prototype.setPublicKey('') // 
  JsEncrypt.prototype.setPrivateKey(privateKey.value)
  const encrypt = JsEncrypt.prototype.encrypt(originalText.value)

  if (encrypt === false) {
    ElMessage.error('')
    return
  }

  ciphertext.value = encrypt
  ElMessage({ message: '', type: 'success' })
}

// 
const privateKeyDecrypt = () => {
  if (privateKey.value === null || privateKey.value === '') {
    ElMessage.error('')
    return
  }

  if (ciphertext.value === null || ciphertext.value === '') {
    ElMessage.error('')
    return
  }

  JsEncrypt.prototype.setPublicKey('') // 
  JsEncrypt.prototype.setPrivateKey(privateKey.value)
  const decrypt = JsEncrypt.prototype.decrypt(ciphertext.value)

  if (decrypt === false) {
    ElMessage.error('')
    return
  }

  if (decrypt === null) {
    ElMessage.error('')
    return
  }

  originalText.value = decrypt
  ElMessage({ message: '', type: 'success' })
}

// RSA 
const publicKey = ref(rsaStore.getPublicKey)
watch(() => publicKey.value, (newValue, oldValue) => {
  rsaStore.setPublicKey(newValue)
})

// RSA 
const privateKey = ref(rsaStore.getPrivateKey)
watch(() => privateKey.value, (newValue, oldValue) => {
  rsaStore.setPrivateKey(newValue)
})

//  Notification
const applyNotification = (bitsValue: number) => {
  if (bitsValue === 4096) { // 4096  Notification 
    if (!('Notification' in window)) { // 
      //  Notification
      console.error(' Notification')

      // eslint-disable-next-line brace-style
    }
    // 
    else if (Notification.permission === 'granted') { //  Notification
      console.log(' Notification')
    } else if (Notification.permission !== 'denied') { //  Notification
      console.log(' Notification')
      Notification.requestPermission().then(function (permission) { //  Notification
        if (permission === 'granted') { //  Notification
          console.log('Notification ')
        } else { //  Notification
          console.log('Notification ')
        }
      })
    }
  }
}

// RSA
const bits = ref<number>(1024)
watch(() => bits.value, (newValue, oldValue) => {
  applyNotification(newValue)
})

const bitsOptions = [
  {
    value: 512,
    label: '512 ()'
  },
  {
    value: 1024,
    label: '1024 ()'
  },
  {
    value: 2048,
    label: '2048 ()'
  },
  {
    value: 4096,
    label: '4096 ()'
  }
]

// 
const generate = async () => {
  if (warning.value) {
    exec()
  } else {
    if (bits.value === 2048 || bits.value === 4096) {
      ElMessageBox.confirm(` JavaScript  ${bits.value}  RSA ${bits.value === 2048 ? '' : ''}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        warning.value = true
        exec()
      }).catch(() => {

      })
    } else {
      exec()
    }
  }
}

const exec = () => {
  const rsaKeypair = jsrsasign.KEYUTIL.generateKeypair('RSA', bits.value)
  publicKey.value = jsrsasign.KEYUTIL.getPEM(rsaKeypair.pubKeyObj)
  privateKey.value = jsrsasign.KEYUTIL.getPEM(rsaKeypair.prvKeyObj, 'PKCS8PRV')

  if ('Notification' in window) { // 
    if (Notification.permission === 'granted') { // 
      const notification = new Notification(` ${bits.value}  RSA `)
      notification.onshow = function () {

      }
    }
  }
}

</script>

<style lang="scss">

.generate-button {
  // 0
  border-top-right-radius: 0;
  // 0
  border-bottom-right-radius: 0;
}

.generate-button-select .el-input__wrapper {
  // 0
  border-top-left-radius: 0;
  // 0
  border-bottom-left-radius: 0;
}

</style>

<style scoped>

.generate-button {
  width: calc(100% - 150px);
}

</style>
